exports.default = {
    name: 'addTopic',
    mounted: function mounted() {
        var self = this;
        var data = self.$route.query.data;
        if (data) {
            self.addTitle = '立即修改';
            self.formData = data;
            self.method = 'PUT';
        } else {
            self.method = 'POST';
        }
    },
    data: function data() {
        return {
            addTitle: '立即添加',
            options: [{
                value: 1,
                label: '算力'
            }, {
                value: 2,
                label: '积分'
            }, {
                value: 3,
                label: '经验'
            }],
            method: '',
            max: 3,
            formData: {
                freeProbability: '',
                pointProbability: '',
                rewardQuantity: '',
                rewardType: ''
            },
            rules: {
                freeProbability: [{
                    required: true, message: '请输入免费概率', trigger: 'blur'
                }],
                pointProbability: [{
                    required: true, message: '请输入积分概率', trigger: 'blur'
                }],
                rewardQuantity: [{
                    required: true, message: '请输入奖励数量', trigger: 'blur'
                }],
                rewardType: [{
                    required: true, message: '请选择奖励类型', trigger: 'blur'
                }]
            }
        };
    },

    methods: {
        submitForm: function submitForm(formName) {
            var self = this;
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    self.$ajax({ url: 'rewardSector/', method: self.method, data: self.formData }).then(function (response) {
                        if (response.code === 1) {
                            self.$notify({
                                title: '成功',
                                message: '题目添加成功',
                                type: 'success',
                                duration: 1000
                            });
                        }
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm: function resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    }
};

